import React from 'react';
import './header.styles.scss';
import Carousel from 'react-bootstrap/Carousel';
import Nav from 'react-bootstrap/Nav';
import cluj1 from '../../assets/cluj1.jpg';
import cluj2 from '../../assets/cluj2.jpg';
import test from '../../assets/test.png';
import homepageImage from '../../assets/homepageImage.jpg';

const Header = () => (
    <div className="header-container">
        <Nav className="navbar">
            <div className="nav-left">
                <Nav.Item>
                    <Nav.Link href="/home">
                        <img id='logo-img' src={test} alt="Bulgariatelep church"></img>    
                    </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <div className="logo-text">
                        <p>Bulgáriatelepi</p>
                        <p>Református Gyülekezet</p>
                    </div>
                </Nav.Item>
            </div>
            <div className="nav-right">
                <Nav.Item className="nav-item">
                    <Nav.Link className="option" href="/contact">Rólunk</Nav.Link>
                </Nav.Item>

                <Nav.Item>
                    <Nav.Link className="option" href="/contact">Prédikációk</Nav.Link>
                </Nav.Item>

                <Nav.Item>
                    <Nav.Link className="option" href="/contact">Elérhetőség</Nav.Link>
                </Nav.Item>
            </div>
        </Nav>
        <Carousel className="carousel">
            <Carousel.Item interval={4500}>
                <img
                    className="d-block w-100"
                    src={cluj1}
                    alt="First slide"
                    style={{ height: '40rem', filter: 'brightness(60%)'}}
                />
                <Carousel.Caption className="caption">
                    <h3>Kolozsvár</h3>
                    <p>A kincses város</p>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item interval={4500}>
                <img
                    className="d-block w-100"
                    src={homepageImage}
                    alt="Second slide"
                    style={{ height: '40rem', filter: 'brightness(60%)'}}
                />
                <Carousel.Caption className="caption">
                    <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item interval={4500}>
                <img
                    className="d-block w-100"
                    src={cluj2}
                    alt="Third slide"
                    style={{ height: '40rem', filter: 'brightness(60%)'}}
                />
                <Carousel.Caption className="caption">
                    <h3>Third slide label</h3>
                    <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                </Carousel.Caption>
            </Carousel.Item>

        </Carousel>


    </div>
)



export default Header;

