import React from "react";
import './footer.styles.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faYoutube,
  faFacebook,
  faInstagram
} from "@fortawesome/free-brands-svg-icons";

const Footer = () => (
    <div class="social-container">
          <h5>Copyright © 2021 Bulgáriatelepi Református Gyülekezet</h5>
          <div>
            <a href="www.youtube.com" target="_blank" rel="noopener noreferrer"
            className="youtube social">
            <FontAwesomeIcon icon={faYoutube} size="2x" />
            </a>
          <a href="https://www.facebook.com/BulgariaTelep/" target="_blank" rel="noopener noreferrer"
            className="facebook social">
            <FontAwesomeIcon icon={faFacebook} size="2x" />
          </a>
          <a href="https://www.instagram.com/" target="_blank" rel="noopener noreferrer"
            className="instagram social">
            <FontAwesomeIcon icon={faInstagram} size="2x" />
          </a>
          </div>
          <div>
            <form>
                <label className="display-label">
                    Admin: 
                    <input id="user_input" type="text" name="user"/>
                </label>
                <input id="submit-button" type="submit" value="Submit" />
            </form>
      </div>
    </div>  
)

export default Footer;