import React from 'react';
import './sectionBox.styles.scss';

const SectionBox = (props) => (

    <div className={`${props.size}`}>
        <div className="section_box">
            <img src={props.src} alt={props.alt}/>

            <div className='box_container'>
                <div className="o_box">
                    <h1 className="box_text">{props.box_text.toUpperCase()}</h1>
                </div>
                <div className='o_title'>
                    <p className='o1_text'>{props.p1}</p>
                    <p className='o2_text'>{props.p2}</p>
                </div>
            </div>
        </div>
    </div>

)


export default SectionBox;