import { Component } from 'react';
import ReactPlayer from "react-player";
import './videoPlayer.styles.scss';

class ResponsivePlayer extends Component {
  render () {
    return (
      <div className="fb-container" style={{ width: '60rem', height: '30rem' }}>

          <div className="video-text-info">
            <h2>Szolgált: Visky János</h2>
            <p> Dátum: 2021 április 18</p>
          </div>

          <ReactPlayer
            className='react-player'
            url='https://www.facebook.com/BulgariaTelep/videos/1160889287682890'
          />

      </div>
    )
  }
}

export default ResponsivePlayer;