import React, { Component } from "react";
import rp from "request-promise";
import cheerio from "cheerio";

import './napiIge.styles.scss';

class NapiIge extends Component {
    state = { names: [] };
  
    componentDidMount() {
      // use the request-promise library to fetch the HTML from site
      rp("https://thingproxy.freeboard.io/fetch/https://www.evangelikus.hu/oldal/a-mai-nap-igei")
        .then(html => {
          let names = [];
          let $ = cheerio.load(html);
  
          // find what element ids, classes, or tags you want from opening console in the browser
          // cheerio library lets you select elements similar to querySelector
          $("p:nth-child(5), p:nth-child(6), p:nth-child(7), p:nth-child(8)").each(function(i, element) {
            let name = $(this)
              .prepend()
              .text();
            names.push(name);
          });
  
          this.setState({ names });
        })
        .catch(function(err) {
          console.log("crawl failed");
        });
    }

    render() {
        const message = () => {
            window.open('https://www.evangelikus.hu/oldal/a-mai-nap-igei');
           }
        return(
            <div className="verse-wrapper">

                <div className="verses">
                    <div id="first">
                        <h4>"{this.state.names[1]}"</h4>
                    </div>
                    <div id="second">
                        <h4>"{this.state.names[2]}"</h4>
                    </div>
                    <div id="reading">
                        <h5>{this.state.names[3]}</h5>
                    </div>
                    <div className="follow">
                        <h5>Figyelmedbe ajánljuk hogy olvasd tovább az Evangélikus egyház napi áhitatját amelyet elolvashatsz ha </h5>
                        <button onClick={message}> ide kattintasz. </button>                
                    </div>

                </div>
        </div>
      )

    }
}

export default NapiIge;