import React from "react";

import './signup.styles.scss';

class SignUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            username: ''
        }
    }

    render() {
        return (
            <div className="signup-container">
                <h1 className='s-title'>ÍRATKOZZON FEL A HÍRLEVELÜNKRE !</h1>
                <form className="form">
                    <input type="text" name="username" placeholder="Keresztnév" required />
                    <input type="email" name="email"  value={this.state.email} placeholder="E-mail" required />
                    <input type="submit" value="FELIRATKOZOM" />
                </form>
            </div>
        )
    }
}

export default SignUp;